<template>
	<el-form ref="form" :model="form" :rules="rules" label-width="0">
		<el-form-item label="" prop="username">
			<el-input ref="username" v-model="form.username" placeholder="手机号" autocomplete="off" @keyup.enter.native="login('form')" />
		</el-form-item>
		<el-form-item label="" prop="code">
			<el-input v-model="form.code" placeholder="4位验证码" @keyup.enter.native="login('form')">
				<el-button slot="append" type="text" @click="sendCode">{{ message }}</el-button>
			</el-input>
		</el-form-item>
		<el-form-item>
			<el-button class="login-btn" type="primary" @click.native="login('form')">登录</el-button>
			<!-- <el-button class="login-btn" @click.native="$emit('toRegister')">注册</el-button> -->
		</el-form-item>
	</el-form>
</template>

<script>
    import Util from '@/util/index'
    import { sendMsg, userRegister } from '@/api/user'
    export default {
    	data() {
    		return {
    			hasSend: false,
    			form: {
    				username: '',
    				password: '',
    				code: ''
    			},
    			count: 60,
    			rules: {
    				username: [{ validator: new Util().validateUsername, trigger: 'blur' }],
    				code: [{ validator: new Util().validateCode, trigger: 'blur' }]
    			}
    		}
    	},
    	computed: {
    		message() {
    			return this.hasSend ? `倒计时${this.count}S` : '发送验证码'
    		}
    	},
    	methods: {
    		/**
    		 * 发送短信
    		 */
    		sendCode() {
    			new Util().validateUsername(null, this.form.username, async err => {
    				if (err !== undefined) {
    					this.$message.error(err.message)
    				} else {
    					if (this.hasSend) return false
    					// send code
    					const result = await sendMsg({ phone: this.form.username, type: 'login' })
    					// console.log(result)
    					this.hasSend = true
    					this.cutDown()
    				}
    			})
    		},
    		/**
    		 * 倒计时
    		 */
    		cutDown() {
    			const interval = setInterval(() => {
    				--this.count
    				if (this.count < 0) {
    					this.hasSend = false
    					clearInterval(interval)
    				}
    			}, 1000)
    		},
    		/**
    		 * 登录
    		 */
    		login(formName) {
    			this.$refs[formName].validate(async valid => {
    				if (valid) {
    				}
    			})
    		}
    	}
    }
</script>

