    .el-form {
    	width: 70%;
    	margin: 0 auto;
    	text-align: center;
    }
    .el-button.login-btn {
    	padding: 16px 20px;
    	margin-top: 40px;
    	margin-left: 0;
    	width: 100%;
    }
</style>
