<template>
	<el-form ref="form" :model="form" :rules="rules" label-width="0">
		<el-tabs v-model="active" stretch>
			<el-tab-pane label="注册" name="1">
				<el-form-item label="" prop="username">
					<el-input v-model="form.username" placeholder="手机号" autocomplete="off" @keyup.enter.native="register('form')" />
				</el-form-item>
				<el-form-item label="" prop="code">
					<el-input v-model="form.code" placeholder="4位验证码" @keyup.enter.native="register('form')">
						<el-button slot="append" @click.native="sendCode">{{ message }}</el-button>
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="password">
					<el-input v-model="form.password" type="password" placeholder="密码(6-12位数字字母)" autocomplete="off" show-password @keyup.enter.native="register('form')" />
				</el-form-item>
				<el-form-item label="" prop="checkPassword">
					<el-input v-model="form.checkPassword" type="password" placeholder="验证密码(6-12位数字字母)" autocomplete="off" show-password @keyup.enter.native="register('form')" />
				</el-form-item>
			</el-tab-pane>
		</el-tabs>
		<el-form-item>
			<el-button class="login-btn" type="primary" @click.native="register('form')">注册</el-button>
			<el-button class="login-btn" @click.native="$emit('toLogin')">登录</el-button>
		</el-form-item>
	</el-form>
</template>

<script>
    import Util from '@/util/index'
    import { sendMsg, userRegister } from '@/api/user'
    export default {
    	data() {
    		const validateCheckPass = (rule, val, callback) => {
    			if (val !== this.form.password) {
    				callback(new Error('两次密码不一致'))
    			} else {
    				callback()
    			}
    		}
    		return {
    			active: '1',
    			hasSend: false,
    			form: {
    				username: '',
    				password: '',
    				checkPassword: '',
    				code: ''
    			},
    			rules: {
    				username: [{ validator: new Util().validateUsername, trigger: 'blur' }],
    				password: [{ validator: new Util().validatePass, trigger: 'blur' }],
    				code: [{ validator: new Util().validateCode, trigger: 'blur' }],
    				checkPassword: [
    					{ validator: new Util().validatePass, trigger: 'blur' },
    					{ validator: validateCheckPass, trigger: 'blur' }
    				]
    			}
    		}
    	},
    	computed: {
    		message() {
    			return this.hasSend ? '60S' : '发送验证码'
    		}
    	},
    	methods: {
    		register(formName) {
    			this.$refs[formName].validate(async valid => {
    				if (valid) {
    					if (this.form.checkPassword !== this.form.password) {
    						this.$message({
    							type: 'warning',
    							msg: '验证密码与密码不一致'
    						})
    						return false
    					}
    					const { username, password, code } = this.form
    					await userRegister({ username, password, code })
    					this.$refs[formName].resetFields()
    					this.$emit('toLogin')
    				}
    			})
    		},
    		sendCode() {
    			new Util().validateUsername(null, this.form.username, async err => {
    				if (err) {
    					this.$message.error(err.message)
    				} else {
    					const result = await sendMsg({ phone: this.form.username, type: 'register' })
    					this.hasSend = true
    				}
    			})
    		}
    	}
    }
</script>

<style lang="scss" scoped>
    .el-form {
    	width: 70%;
    	margin: 0 auto;
    	text-align: center;
    }
    .el-button.login-btn {
    	padding: 16px 20px;
    	margin-top: 40px;
    	margin-left: 0;
    	width: 100%;
    }
</style>
