<template>
	<el-form ref="form" :model="form" :rules="rules" label-width="0">
		<el-form-item label="" prop="username">
			<el-input v-model="form.username" placeholder="手机号" autocomplete="off" @keyup.enter.native="login('form')" />
		</el-form-item>
		<el-form-item label="" prop="password">
			<el-input v-model="form.password" type="password" placeholder="密码(6-12位数字字母)" autocomplete="off" show-password @keyup.enter.native="login('form')" />
		</el-form-item>
		<el-form-item>
			<el-button class="login-btn" type="primary" @click="login('form')">登录</el-button>
			<!-- <el-button class="login-btn" @click="$emit('toRegister')">注册</el-button> -->
		</el-form-item>
	</el-form>
</template>

<script>
    import Util from '@/util'
    import { userLogin } from '@/api/user'
    export default {
    	data() {
    		return {
    			form: {
    				username: '',
    				password: ''
    			},
    			rules: {
    				username: [{ validator: new Util().validateUsername, trigger: 'blur' }],
    				password: [{ validator: new Util().validatePass, trigger: 'blur' }]
    			}
    		}
    	},
    	methods: {
    		login(formName) {
    			this.$refs[formName].validate(async valid => {
    				if (valid) {
    					await this.$store.dispatch('login', { username: this.form.username, password: this.form.password })
    				}
    			})
    		}
    	}
    }
</script>

<style lang="scss" scoped>
    .el-form {
    	width: 70%;
    	margin: 0 auto;
    	text-align: center;
    }
    .el-button.login-btn {
    	padding: 16px 20px;
    	margin-top: 40px;
    	margin-left: 0;
    	width: 100%;
    }
</style>
